// import goodsaction from '@/components/goodsaction'
export default {
    data(){
        return {
            show: false,
            shareShow: false,
            ticksShow: false
        }
    },
    components: {
        // goodsaction,
    },
    methods: {
        goBack(){
            this.$router.go(-1);
        },
        gotoComments(){
            this.$router.push({name:'comments'})
        },
        showTick(){
            this.show = true;
            this.ticksShow = true;
        },
        close(){
            this.show = false;
            this.ticksShow = false;
            this.shareShow = false;
        },
        share(){
            this.show = true;
            this.shareShow = true;
        },
        cancel(){
            this.show = false;
            this.shareShow = false;
        }
    }
}
